.cboxIE #cboxBottomCenter,
.cboxIE #cboxBottomRight,
.cboxIE #cboxMiddleLeft,
.cboxIE #cboxMiddleRight {
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#00FFFFFF,endColorstr=#00FFFFFF); }

/*
 * custom changes
 */
#colorbox {
  box-shadow: 0px 0px 25px 0px rgba(50, 50, 50, 0.75); }

#cboxTopCenter {
  height: 10px; }

#cboxBottomCenter {
  height: 10px; }

#cboxMiddleLeft {
  width: 10px; }

#cboxMiddleRight {
  width: 10px; }

#cboxWrapper {
  background-color: #fff; }

#cboxOverlay {
  background-color: #333; }

/*
 * cboxClose
 */
#cboxClose {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0; }
  #cboxClose div {
    position: absolute;
    right: 0;
    top: 0;
    height: 0;
    width: 0;
    border-top: 50px solid white;
    border-left: 50px solid transparent;
    border-bottom: 50px solid transparent; }
  #cboxClose span {
    position: relative;
    top: -1px;
    right: -4px;
    display: block;
    width: 23px;
    height: 23px;
    cursor: pointer;
    z-index: 1103;
    /* ir */
    will-change: transform;
    transition: transform .4s ease-in-out;
    transform: rotate(90deg) scale(1); }
  #cboxClose:hover span {
    transform: rotate(180deg) scale(0.9); }

.colorbox-complete #cboxClose {
  opacity: 1; }

/*
 * previous / next -> pagination
 */
#cboxPrevious,
#cboxNext {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 20%; }
  #cboxPrevious:focus,
  #cboxNext:focus {
    outline: none; }
  #cboxPrevious span,
  #cboxNext span {
    display: block;
    margin: auto;
    opacity: 0;
    width: 40px;
    /* transition */
    transition-property: transform, opacity;
    transition-duration: .3s;
    transition-timing-function: ease-out; }
  #cboxPrevious:hover span,
  #cboxNext:hover span {
    opacity: 1; }

#cboxPrevious {
  left: 0; }
  #cboxPrevious span {
    margin-left: 20%; }
  #cboxPrevious:hover span {
    transform: translateX(-10px); }

#cboxNext {
  right: 0; }
  #cboxNext span {
    float: right;
    margin-right: 20%; }
  #cboxNext:hover span {
    transform: translateX(10px); }

/*
 * cboxCurrent Page
 */
#cboxCurrent {
  display: none !important; }

/*
 * cbox Title Description
 */
#cboxTitleMoved {
  margin-top: 14px;
  padding: 0 5px; }

/*
 * ie fixes
 */
.ie8 #cboxPrevious span,
.ie8 #cboxNext span, .ie9 #cboxPrevious span,
.ie9 #cboxNext span, .ie10 #cboxPrevious span,
.ie10 #cboxNext span {
  opacity: 1; }

.ie8 #cboxPrevious:hover span,
.ie8 #cboxNext:hover span, .ie9 #cboxPrevious:hover span,
.ie9 #cboxNext:hover span, .ie10 #cboxPrevious:hover span,
.ie10 #cboxNext:hover span {
  transform: translateX(0) !important; }
